@import url(https://fonts.googleapis.com/css2?family=Scheherazade&display=swap);
body {
background: #ffffff;
margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.grid-container {
  display: inline-flex;
  flex-wrap: wrap;
  justify-content: center;
}

.grid-container > div {
  text-align: center;
  padding: 20px 0;
  font-size: 30px;
  border-bottom: solid 2px #E87756;
  padding-bottom: 10px;
}




.logo{
  border-radius: 40%;
}

.bg{
  font-size: 21px;
  font-family: 'Reem Kufi', sans-serif;

}




nav{
  padding: 10px;
  float: right;
}
 
a{
  text-decoration: none;
  color: aliceblue;
}
.des{
  font-size: 25px;
  font-weight: bolder;

}
.social:hover{
  color:white;
  -webkit-animation: pulse 3s;
          animation: pulse 3s;
}
.container{
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  border-bottom: solid 2px #E87756;
  padding-bottom: 10px;
}

.secondary{
  background-color: #FF9F30; /* Green */
  border-radius: 20%;
  float: left;
  margin: 3px 1px;
  padding: 6px;
  text-align: center;
  font-size: 20px;

}

.first{
  background-color: #FF9F30;
  border-radius: 20%;
  float: right;
  margin: 3px 1px;
  padding: 6px;
  text-align: center;
  font-size: 20px;

}
h1{
  color: goldenrod;
}
 h2{
    font-weight: 1;
    color: goldenrod;
    font-size: 3.5rem;
  }
    p{
  font-family: 'Scheherazade', serif;
  font-weight: bold;
  color: #000000;
}

.mahmoud{
  color: #133ADB;
}
.particles{
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: -500;
  }
